import React from "react";

import {
  Stats,
  BigBreadcrumbs,
  WidgetGrid,
  JarvisWidget
} from "../../../common";

import SparklineContainer from "../../../common/graphs/inline/components/SparklineContainer";

import Datatable from "../../../common/tables/components/Datatable";

export default class Projects extends React.Component {
  render() {
    const options = {
      ajax: "assets/api/projects/project-list.json",
      iDisplayLength: 15,
      columns: [
        {
          class: "details-control",
          orderable: false,
          data: null,
          defaultContent: ""
        },
        { data: "name" },
        { data: "est" },
        { data: "contacts" },
        { data: "status" },
        { data: "target-actual" },
        { data: "starts" },
        { data: "ends" },
        { data: "tracker" }
      ],
      order: [[1, "asc"]]
    };
    return (
      <div id="content">
        <div className="row">
          <BigBreadcrumbs
            items={["Projects", "Overview"]}
            icon="file-text-o"
            className="col-xs-12 col-sm-7 col-md-7 col-lg-4"
          />
          <Stats />
        </div>

        <WidgetGrid>
          {/* row */}
          <div className="row">
            <article className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
              <div className="alert alert-info">
                <strong>NOTE:</strong> All the data is loaded from a seperate
                JSON file
              </div>
              {/* Widget ID (each widget will need unique ID)*/}
              <JarvisWidget className="well" id="projects-widget">
                <header>
                  <span className="widget-icon">
                    <i className="fa fa-comments" />
                  </span>

                  <h2>Widget Title </h2>
                </header>
                {/* widget div*/}
                <div>
                  {/* widget content */}
                  <div className="widget-body no-padding">
                    <SparklineContainer>
                      <Datatable
                        options={options}
                        detailsFormat={detailsFormat}
                        className="display projects-table table table-striped table-bordered table-hover"
                        cellSpacing="0"
                        width="100%"
                      >
                        <thead>
                          <tr>
                            <th />
                            <th>Projects</th>
                            <th>
                              <i className="fa fa-fw fa-user text-muted hidden-md hidden-sm hidden-xs" />
                              EST
                            </th>
                            <th>Contacts</th>
                            <th>Status</th>
                            <th>
                              <i className="fa fa-circle txt-color-darken font-xs" />
                              Target/
                              <i className="fa fa-circle text-danger font-xs" />
                              Actual
                            </th>
                            <th>
                              <i className="fa fa-fw fa-calendar text-muted hidden-md hidden-sm hidden-xs" />
                              Starts
                            </th>
                            <th>
                              <i className="fa fa-fw fa-calendar text-muted hidden-md hidden-sm hidden-xs" />
                              Ends
                            </th>
                            <th>Tracker</th>
                          </tr>
                        </thead>
                        {/*

                                                 */}
                      </Datatable>
                    </SparklineContainer>
                  </div>
                  {/* end widget content */}
                </div>
                {/* end widget div */}
              </JarvisWidget>
            </article>
          </div>
          {/* end row */}
        </WidgetGrid>
      </div>
    );
  }
}

function detailsFormat(d) {
  return `<table cellPadding="5" cellSpacing="0" border="0" class="table table-hover table-condensed">
            <tbody><tr>
                <td style="width:100px">Project Title:</td>
                <td>${d.name}</td>
            </tr>
            <tr>
                <td>Deadline:</td>
                <td>${d.ends}</td>
            </tr>
            <tr>
                <td>Extra info:</td>
                <td>And any further details here (images etc)...</td>
            </tr>
            <tr>
                <td>Comments:</td>
                <td>${d.comments}</td>
            </tr>
            <tr>
                <td>Action:</td>
                <td>${d.action}</td>
            </tr></tbody>
        </table>`;
}
